import { Container, Nav, Navbar} from 'react-bootstrap';
import { Link } from 'react-router-dom';

import logoBlue from "../images/project4logoB.png";
import sns1 from "../images/facebook.png";
import sns2 from "../images/instagram.png";
import sns3 from "../images/naverblog.png";
import sns4 from "../images/kakaotalk.png";

const Header = () => {
    return(
        <header>
            
            <div id='headerTop'>
                <Container>
                    <div className='top1'>
                        <Link to='/member/login'></Link>
                        <Link to='/member/join'></Link>
                        <Link to='/member/mypage'></Link>
                        <Link to='/member/cart'></Link>
                        <Link to='/helpdesk'></Link>
                    </div>
                    <div className='top2'>
                        <Link to='#'><img src={sns1}/></Link>
                        <Link to='#'><img src={sns2}/></Link>
                        <Link to='#'><img src={sns3}/></Link>
                        <Link to='#'><img src={sns4}/></Link>
                    </div>
                </Container>
            </div>
            
            <Navbar collapseOnSelect expand="md" id='headerMenu'>
                <Container>
                <Navbar.Brand href="/">
                    <img src={logoBlue}/>
                </Navbar.Brand>
                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav">
                    <Nav>
                    <Nav.Link href="/application"> </Nav.Link>
                    <Nav.Link href="/courses"> </Nav.Link>
                    <Nav.Link href="/tests"> </Nav.Link>
                    <Nav.Link href="/services"> </Nav.Link>
                    <Nav.Link href="/community"></Nav.Link>
                    <Nav.Link href="/introduction"></Nav.Link>
                    </Nav>
                </Navbar.Collapse>
                </Container>
            </Navbar>
        </header>
    );
}

export default Header;